.header {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 23px 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  padding: 23px 0;
  text-decoration: none;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__item {
  text-transform: uppercase;
  margin-right: 20px;
  font-size: 12px;
  font-weight: 500;
}

.nav__item:last-child {
  margin-right: 0;
}

.img {
  width: 40px;
  height: 40px;
  margin: 10px 0;
  display: block;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.stars {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 4px 0 0;
}

.stars__star:last-child {
  margin: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card:hover {
  transition: all .3s;
  scale: 120%;
}

.card:hover__title {
  color: #34568b;
}

.card__img {
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  color: #060b35;
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__review {
  margin: 0;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-top: 24px;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: inline-block;
}

.card__price-total {
  text-align: right;
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  display: inline-block;
}

.card__button {
  color: #fff;
  text-align: center;
  box-sizing: border-box;
  background-color: #00acdc;
  border-radius: 5px;
  height: 40px;
  margin-top: 16px;
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.catalog {
  justify-content: center;
  padding: 50px 40px;
  display: flex;
}

.catalog__content {
  grid-template-columns: repeat(1, 1fr);
  gap: 46px 48px;
  max-width: 1200px;
  display: grid;
}

@media (width >= 488px) {
  .catalog__content {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .catalog__content {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .catalog__content {
    grid-template-columns: repeat(4, 1fr);
  }
}
